/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Container */
.container {
  display: flex;
  height: 100vh;
  background: #f4f4f4;
}

/* Left & Right sections */
.search-section,
.liked-section {
  flex: 1;
  padding: 20px;
  background: white;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto; /* Enable scrolling */
  padding-bottom: 80px; /* Space for the fixed player */
}

.search-section {
  border-right: 2px solid #ddd;
}

/* Title */
h2 {
  text-align: center;
  margin-bottom: 15px;
}

/* Search Input & Button */
.search-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.search-button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
}

.search-button:hover {
  background-color: #0056b3;
}

.song-info {
  flex-grow: 1; /* Expands to take available space */
  cursor: pointer;
}
/* Song List */
.song-list {
  list-style: none;
  padding: 0;
  max-height: calc(100vh - 200px); /* Adjusting for header and player */
  overflow-y: auto;
}

/* Scrollable Liked Songs */
.liked-section .song-list {
  max-height: calc(100vh - 180px); /* Space for player */
  overflow-y: auto;
}

/* Song Item */
.song-item {
  display: flex;
  align-items: center;
  gap: 10px; /* Reduced gap for better spacing */
  padding: 10px;
  border-radius: 8px;
  background: #f9f9f9;
  transition: background 0.3s ease;
  margin-bottom: 8px;
}

.song-item:hover {
  background: #e6e6e6;
}

/* Fix for song images */
.song-image {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  margin-right: 10px;
}

/* Playing Song Effect */
.song-item.playing {
  background: #f0f8ff; /* Light blue for active song */
}

/* Like Button */
.like-button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: red;
}

/* Seek Bar */
.seek-bar {
  width: 50%;
  cursor: pointer;
}

/* Player Controls - Fixed at the bottom */
.player-controls {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 15px;
  box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

/* Style player control buttons */
.player-controls button {
  background: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s;
}

.player-controls button:hover {
  background: #0056b3;
  color: white;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .container {
    flex-direction: column; /* Stack sections on smaller screens */
    height: auto;
  }

  .search-section,
  .liked-section {
    width: 100%;
    border-right: none;
    padding-bottom: 100px; /* More space for player */
  }

  .player-controls {
    flex-direction: column; /* Stack player controls for better spacing */
    padding: 10px;
    gap: 10px;
  }

  .seek-bar {
    width: 90%;
  }
}

@media screen and (max-width: 480px) {
  .player-controls {
    padding: 10px;
    gap: 8px;
  }

  .player-controls button {
    padding: 8px;
    font-size: 14px;
  }

  .seek-bar {
    width: 80%;
  }
}

.delete-button {
  background: none;
  border: none;
  cursor: pointer;
  /* color: red; */
  font-size: 18px;
  transition: transform 0.2s ease;
  margin-left: auto; /* Pushes the delete button to the right */
}

.delete-button:hover {
  transform: scale(1.2);
}
